  import Step from "../../../base/step/step.vue"
  import Scroll from '../../../base/scroll/scroll.vue'
  import {mapGetters} from 'vuex'

  const typeArr = ['综合排序', '信用', '价格从高到低', '价格从低到高', '销量优先']
  export default {
    name: "taskOne_step1",
    components: {
      Step,
      Scroll
    },
    data() {
      return {
        isOk: true,
        openIt: false,
        isPass: true, //验证店铺按钮
        isRight: false, //验证通过
        isWrong: false, //验证错误
        stepIndex: 0, //当前任务步骤下标
        shopName: "",
        twoInfo: {},
        task: [],
        taskIndex: 0,
        showMsg: {},
        seller: '',
        sellerTaskId: ''
      }
    },
    computed: {
      ...mapGetters([
        'userNopay',
        'userInfo'
      ])
    },
    created() {
      let that = this;
      /*this.$axios.post('/api/orderOperate/getBuyerTask', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId
      }).then((res) => {
        let data = res.data
        if (data.code=== "200") {
          console.log(data)
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title:'错误提示',
          content: '服务器错误',
        })
      })*/

//      第二步的核对商品
      this.$axios.post('/api/orderOperate/getAdditionalInfo', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId
      }).then((res) => {
        let data = res.data
        if (data.code === "200") {
          this.twoInfo = data.data
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '错误提示',
          content: '服务器错误',
        })
      })


      this.$axios.post('/api/orderOperate/listSellerTaskKeyword', {
        sellerTaskId: that.$route.query.sellerTaskId
      }).then((response) => {
        let data = response.data
        if (data.code == 200) {
          console.log(data);
          let arr = data.data
          this.task = arr
          this.taskIndex = this.mathRandom()
          this.changeMsg()
        }
      }).catch((error) => {
        console.log(error)
      })

    },
    methods: {
      open() {
        this.openIt = !this.openIt
        //获取关键词
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      },
//设置taskindex
      //生成数随机数
      mathRandom() {
        let length = this.task.length
        return Math.floor((Math.random() * length))
      },
      //将数据改变成自己的数据
      changeMsg() {
        let nowMsg = this.task[this.taskIndex]
        console.log(nowMsg)
        this.showMsg.discountsKeyword = JSON.parse(nowMsg.discountsKeyword).join(' ')
        this.showMsg.sendPlace = nowMsg.sendPlace
        this.showMsg.sortType = typeArr[(parseInt(nowMsg.sortType) - 1)]
        this.showMsg.price = (nowMsg.priceLower) + "-" + (nowMsg.priceLower) + '元'
        this.showMsg.sellerTaskId = nowMsg.sellerTaskId
        var keyWordObj = JSON.parse(nowMsg.keyword)
        let keyName = ''
        for (let m in keyWordObj) {
          keyName += `${m} `
        }
        this.showMsg.keyName = keyName
        console.log(this.showMsg)
      },
      onInput() {
        //如果通过显示通过按钮
        if (this.seller === this.twoInfo.shopName) {
          this.isPass = false
          this.isRight = true
          this.isWrong = false
        } else {
          this.isPass = false
          this.isRight = false
          this.isWrong = true
        }
      },

      showIsPass() {
        this.isPass = true
        this.isRight = false
        this.isWrong = false
      },
      doNext() {
        this.$router.push({name: 'taskTwoStep2', query: {buyerTaskRecordId: this.$route.query.buyerTaskRecordId}})
      }
    }
  }



